 *, *::before, *::after {
      box-sizing: border-box;
    }
    .wrapper {
      user-select: none;
    }
    h1 {
      text-align: center;
      text-transform: capitalize;
      font-family: cursive;
      font-size: 1.3rem;
    }
    .drag-and-drop {
      display: flex;
      flex-flow: row wrap;
    }
    .draggable-container {
      width: 20%;
      background: aliceblue;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .droppable-container {
      width: 80%;
      background: #f9f9f9;
      padding-top: 50px;
      list-style: auto inside;
    }
    .droppable-container .sentense {
      padding: 5px;
    }
    .element {
      font-family: serif;
      padding: 10px 20px;
      background: #903779;
      color: #fff;
      display: inline-block;
      margin: 5px;
      cursor: move;
    }
    .element.-dragged {
      opacity: .5;
    }
    .box {
      vertical-align: middle;
      text-align: center;
    }
    .box.droppable {
      display: inline-block;
      text-indent: 0px;
      min-width: 150px;
      min-height: 2rem;
    }
    .box.droppable:empty {
      background-color: #d9dddc;
    }
    .box.droppable.-active {
      min-height: 3rem;
      background: orange;
    }
    .droppable.-wrong {
      border: 3px solid #FF005A;
    }
    .droppable.-correct {
      border: 3px solid rgb(9, 231, 9);
    }
    .droppable.-correct > span {
      cursor: not-allowed;
    }
 
    .box > .element {
      margin: 0;
      padding: 4px;
      width: 100%;
      height: 100%;
    }
    @media only screen and (max-width: 624px) {
      h1 {
        font-size: 1.4rem;
      }
      .drag-and-drop {
        flex-direction: column;
      }
      .draggable-container {
        width: 100%;
        flex-flow: row wrap;
        justify-content: center;
      }
      .droppable-container {
        width: 100%;
      }
    }